  private show=false
  
  private memolist=[
    {"id":1,"name":"todo","content":"aaa"},
    {"id":2,"name":"resutrant","content":"aaa"},
    {"id":3,"name":"book","content":"aaa"},
  ]
  private created(){
    this.pass=
    this.pass.split('')
    .map(c => {
      switch (c) {
        case 'x': return (Math.random() * 16 | 0).toString(16);
        case 'y': return ((Math.random() * 4 | 0) + 8).toString(16);
        default: return c;
      }
    })
    .join('')
    console.log(this.pass)
    this.loginID=this.pass

    fetch("".concat(`https://challenge-server.tracks.run/memoapp`,"/memo"),
    {headers:{"X-ACCESS-TOKEN":this.pass,
    "Content-Type":"application/json"},
    method:"POST",
    body:JSON.stringify(this.memolist)})
    .then((res)=>{console.log(res)})
    .catch((e)=>{
      console.log(e)
    })
  }

  private get check():boolean{
      if(this.pass===this.loginID){
          return !this.disabled
      }
      return this.disabled
  }

  private login(){
    this.disabled=!this.disabled
    this.success=!this.success
    this.show=!this.show
    fetch(`https://challenge-server.tracks.run/memoapp/category`)
    .then((res)=>{console.log(res)})
    .catch((e)=>{
      console.log(e)
    })
  }
  
  private save(){
    fetch("".concat(`https://challenge-server.tracks.run/memoapp`,`/memo/${1}`),
